import { PageReference } from './page';

export interface ExperimentReference {
    type: 'experiments';
    id: string;
}

export interface ExperimentAttributes {
    title: string;
    description: string;
    status: string;
}

export interface ExperimentRelationships {
    firstPage?: { data: PageReference };
    pages: { data: PageReference[] };
}

export class Experiment {
    public id = '';

    private attributes = {
        title: '',
        description: '',
        status: ''
    } as ExperimentAttributes;

    private relationships = {
        firstPage: undefined,
        pages: {
            data: [] as PageReference[],
        },
    } as ExperimentRelationships;

    public constructor(attributes: ExperimentAttributes, relationships: ExperimentRelationships) {
        this.attributes = attributes;
        this.relationships = relationships;
    }

    public get title(): string {
        return this.attributes.title;
    }

    public set title(newTitle: string) {
        this.attributes.title = newTitle;
    }

    public get description(): string {
        return this.attributes.description;
    }

    public set description(newDescription: string) {
        this.attributes.description = newDescription;
    }

    public get status(): string {
        return this.attributes.status;
    }

    public set status(newStatus: string) {
        this.attributes.status = newStatus;
    }
}
